nav {
    position: absolute;
    z-index: 1001;
    width: 100%;
    height: 7rem;
}

/* Navigation bar links */
.navbar-links2 {
    display: flex;
    width: 100%;
    height: 100%;
    align-items: center;
    justify-content: center;
}

/* Navigation items */
.nav-items2 {
    display: flex;
    width: 100%;
    align-items: center;
    justify-content: space-between;
    margin: 0 calc(7rem - 20px);
}

/* Styles for main navigation options */
.mainNav-options {
    display: flex;
    margin: 0;
}

/* Active item in navigation */
.nav-item #activeItem {
    border-bottom: 2px solid var(--color-text-light);
}

/* Logo image */
.logo-image {
    width: 150px;
    height: auto;
    cursor: pointer;
    filter: drop-shadow(7.07px 7.07px 5px #404040);
}

/* Styles for main navigation links */
.mainNav-options .nav-item #mainNavA {
    font-weight: 600;
    font-size: 18px;
    padding: 20px;
    border-radius: 30px;
    color: var(--color-text-light);
    cursor: pointer;
    text-decoration: none;
    transition: 0.1s ease-in;
    white-space: nowrap;
}

/* Hover effect for main navigation links */
.nav-item #mainNavA:hover {
    background-color: var(--color-darkgreen-ui);
}

/* Hover effect for 'Ajanvaraus' navigation link */
.nav-item #mainNavA.Ajanvaraus:hover {
    background-color: var(--color-orange);
}

/* Hidden elements for small screen menu */
.SMenuBtn2,
.minmenu-logo2 {
    display: none;
}

/* Media query for screens with max-width: 1110px */
@media (max-width: 1110px) {
    .logo-image {
        width: 120px;
        height: auto;
    }

    .navibar {
        padding: 1rem;
    }

    .navbar-links2 {
        justify-content: space-between;
    }

    .navibar-links_logo {
        display: block;
    }

    .nav-items2 {
        display: flex;
        position: fixed;
        z-index: 999;
        width: 100%;
        height: 100%;
        top: 0;
        left: 0;
        flex-flow: column;
        background-color: var(--color-darkshadow);
        opacity: 0;
        transform: translateY(calc(-100%));
        transition: transform 500ms ease-in-out;
    }

    /* Styles for main navigation overlay */
    .mainnavoverlay2 {
        z-index: -1;
        position: absolute;
        display: flex;
        flex-flow: column;
        justify-content: center;
        padding: 3rem;
        top: 0;
        left: 0;
        width: 100%;
        background-color: var(--color-darkgreen-ui);
        clip-path: polygon(49% 0, 100% 0, 100% 100%, 51.5% 100%);
    }

    /* Styles for active navigation items */
    .nav-items2.active2 {
        display: flex;
        position: fixed;
        z-index: 999;
        margin: 0;
        width: 100%;
        height: 100%;
        top: 0;
        left: 0;
        flex-flow: column;
        justify-content: center;
        align-items: center;
        background-color: var(--color-nav-dark);
        opacity: 1;
        transform: translateY(0);
        transition: transform 500ms ease-in-out;
    }

    /* Styles for small screen main navigation options */
    .mainNav-options {
        flex-flow: column;
        align-items: center;
    }

    /* Styles for main navigation item */
    .nav-item {
        margin: 0;
        padding: 2rem 0;
        height: 100%;
        width: 100%;
        color: var(--color-text-light);
        text-align: center;
        transition: all 100ms ease 0s;
    }

    /* Styles for main navigation links */
    .mainNav-options .nav-item #mainNavA {
        font-weight: 500;
        font-size: 20px;
        padding: 2rem 100vw;
        width: 100%;
        height: 100%;
        border-radius: 0;
        color: var(--color-text-light);
    }

    /* Hover effect for main navigation links */
    .mainNav-options .nav-item #mainNavA:hover,
    .mainNav-options .nav-item #mainNavA:active {
        background-color: var(--color-darkgreen-ui);
    }

    /* Hover effect for 'Ajanvaraus' navigation link in the right-aligned menu */
    .mainNav-options.right .nav-item #mainNavA.Ajanvaraus:hover,
    .mainNav-options.right .nav-item #mainNavA.Ajanvaraus:active {
        background-color: var(--color-orange);
    }

    /* Styles for small screen menu button */
    .SMenuBtn2 {
        display: flex;
    }

    /* Styles for menu button */
    #MenuButton2 {
        display: flex;
        align-items: center;
        border: none;
        z-index: 1001;
        cursor: pointer;
        padding: 5px;
        border-radius: 5px;
        font-size: 18px;
        font-weight: 600;
        color: var(--color-text-light);
    }

    /* Styles for the menu button span */
    #MenuButton2 span {
        padding-right: 0.5rem;
    }

    /* Styles for hidden menu button */
    #NoneMenuButton2 {
        display: none;
    }

    /* Styles for close button */
    #CloseButton2 {
        display: flex;
        border: none;
        z-index: 1001;
        cursor: pointer;
        padding: 5px;
        border-radius: 5px;
        color: var(--color-text-light);
    }

    /* Styles for hidden close button */
    #NoneCloseButton2 {
        display: none;
    }

    /* Styles for logo image in small screen menu */
    .logo-image2 {
        display: none;
    }

    /* Styles for small screen menu logo */
    .minmenu-logo2 {
        z-index: 2000;
        display: block;
        width: 130px;
        height: auto;
        margin-left: 3rem;
        cursor: pointer;
    }
}