.wrapper.compbox_tietoja .page-sidebar {
    display: flex;
    flex-direction: column;
    align-items: center;
}
.page-sidebar img{
    margin-top: 2rem;
    border-radius: 200px;
    height: 200px;
    width: 200px;
    object-fit: contain;
}
.page-sidebar p{
    margin-top: 2rem;
    color: var(--color-text-dark);
    font-size: medium;
    text-align: center;
}
.page-sidebar h5 {
    width: 350px;
    color: #f4bc25;
    font-weight: 300;
    text-align: center;
    font-family: 'Playfair Display', tahoma, arial, verdana;
}
.wrapper .page-bodytext a,
.wrapper .page-sidebar a {
    color: var(--color-orange);
}