nav {
    position: absolute;
    z-index: 1001;
    width: 100%;
    height: 6rem;
}

.navbar-links {
    display: flex;
    width: 100%;
    height: 100%;
    align-items: center;
    justify-content: center;
}

.navoverlay2 {
    z-index: -1;
    position: absolute;
    display: flex;
    flex-flow: column;
    justify-content: center;
    padding: 3rem;
    top: 0;
    left: 0;
    width: 100%;
    clip-path: polygon(45% 0, 100% 0, 100% 100%, 49% 100%);
    background-color: var(--color-darkgreen-ui);
}

.nav-options {
    display: flex;
    margin: 0;
}

.nav-items {
    display: flex;
    width: 100%;
    align-items: center;
    justify-content: space-between;
    margin: 0 calc(7rem - 20px);
}

.nav-item #activeItem {
    border-bottom: 2px solid var(--color-text-darkgreen);
}

.logo-image {
    width: 150px;
    height: auto;
    cursor: pointer;
    filter: drop-shadow(7.07px 7.07px 5px #404040);
}

.nav-item #navA {
    font-weight: 600;
    font-size: 18px;
    padding: 20px;
    border-radius: 30px;
    color: var(--color-text-light);
    cursor: pointer;
    text-decoration: none;
    transition: 0.1s ease-in;
    white-space: nowrap;
}

.nav-options.right .nav-item #navA {
    color: var(--color-text-dark);
}

.nav-item #navA:hover {
    color: var(--color-text-darkgreen);
    background-color: var(--color-text-light);
}

.nav-options.right .nav-item #navA:hover {
    background-color: var(--color-darkgreen-ui);
    color: var(--color-text-light);
}

.nav-options.right .nav-item #navA.Ajanvaraus:hover {
    background-color: var(--color-orange);
}

.SMenuBtn,
.minmenu-logo {
    display: none;
}

@media (max-width: 1400px) {
    .navoverlay2 {
        clip-path: polygon(25% 0, 100% 0, 100% 100%, 29% 100%);
    }
}

@media (max-width: 1110px) {
    .nav-options {
        display: flex;
        margin: 0;
    }

    .logo-image {
        width: 120px;
        height: auto;
    }
}

@media (max-width: 970px) {
    .navibar {
        padding: 1rem;
    }

    .navbar-links {
        justify-content: space-between;
    }

    .navibar-links_logo {
        display: block;
    }

    .navoverlay2 {
        clip-path: polygon(45% 0, 100% 0, 100% 100%, 49% 100%);
    }

    .nav-items {
        display: flex;
        position: fixed;
        z-index: 999;
        width: 100%;
        height: 100%;
        top: 0;
        left: 0;
        flex-flow: column;
        background-color: var(--color-darkshadow);
        opacity: 0;
        transform: translateY(calc(-100%));
        transition: transform 500ms ease-in-out;
    }

    .nav-items.active {
        display: flex;
        position: fixed;
        z-index: 999;
        margin: 0;
        width: 100%;
        height: 100%;
        top: 0;
        left: 0;
        flex-flow: column;
        justify-content: center;
        align-items: center;
        background-color: var(--color-nav-dark);
        opacity: 1;
        transform: translateY(0);
        transition: transform 500ms ease-in-out;
    }

    .nav-options {
        flex-flow: column;
        align-items: center;
    }

    .nav-item {
        margin: 0;
        padding: 2rem 0;
        height: 100%;
        width: 100%;
        color: var(--color-text-light);
        text-align: center;
        transition: all 100ms ease 0s;
    }

    .nav-options .nav-item #navA {
        font-weight: 500;
        font-size: 20px;
        padding: 2rem 100vw;
        width: 100%;
        height: 100%;
        border-radius: 0;
        color: var(--color-text-light);
    }

    .nav-options.right .nav-item #navA {
        color: var(--color-text-light);
    }

    .nav-options .nav-item #navA:hover {
        background-color: var(--color-darkgreen-ui);
    }
    .nav-options .nav-item #navA.Ajanvaraus:hover {
        background-color: var(--color-orange);
    }
    .SMenuBtn {
        display: flex;
    }

    #MenuButton {
        display: flex;
        border: none;
        z-index: 1001;
        cursor: pointer;
        padding: 1rem;
        border-radius: 50px;
        color: var(--color-text-light);
        background-color: var(--color-darkgreen-ui);
    }

    #NoneMenuButton {
        display: none;
    }

    #CloseButton {
        display: flex;
        border: none;
        z-index: 1001;
        cursor: pointer;
        padding: 5px;
        border-radius: 5px;
        color: var(--color-text-light);
    }

    #NoneCloseButton {
        display: none;
    }

    .logo-image {
        display: none;
    }

    .minmenu-logo {
        z-index: 2000;
        display: block;
        width: 130px;
        height: auto;
        margin-left: 3rem;
        cursor: pointer;
    }
}