section#services_etusivu {
    display: flex;
    flex-wrap: wrap;
    height: 100%;
    min-height: 95vh;
    justify-content: center;
    align-items: center;
}
.servicecard {
    display: flex;
    flex-flow: column;
    width: 300px;
    margin: 5rem 4rem;
    text-align: center;
    align-items: center;
    border-radius: 30px;
    cursor: pointer;
}
.servicecard > figure {
    display: flex;
    height: 475px;
    width: 300px;
    flex-direction: column;
    position: relative;
    align-items: center;
    border-radius: 32px;
    transition: 100ms ease-in-out;
}
.servicecard > figure:hover,
.servicecard > figure:active {
    box-shadow: 0 1px 19px 0 rgba(0, 0, 0, 0.622), 0 6px 20px 0 rgba(0, 0, 0, 0.19);
}
.servicecard > figure > figcaption {
    position: relative;
    display: flex;
    justify-content: center;
    align-items: center;
    width: 150px;
    height: 150px;
    font-weight: 600;
    border-radius: 150px;
    top: -50px;
    color: var(--color-text-light);
}
.servicecard_img {
    position: absolute;
    height: 100%;
    width: 100%;
    object-fit: cover;
    border-radius: 2rem;
}
.servicecard > button {
    position: relative;
    width: 200px;
    height: 40px;
    font-weight: 500;
    top: -5rem;
    background-color: var(--color-darkgreen-ui);
    border: none;
    border-radius: 50px;
    cursor: pointer;
    transition: 100ms ease-in-out;
    color: var(--color-text-light);
}
.servicecard > button:hover {
    background-color: var(--color-orange);
    font-weight: 600;
}
@media (max-width: 1110px) {
    section#services_etusivu {
        display: flex;
        flex-wrap: wrap;
        height: 100%;
        padding-top: 2rem;
    }
}