#Q_cont {
    display: flex;
    min-height: 75vh;
    height: 100%;
    align-items: center;
    justify-content: center;
    background-image: url(/public/assets/images/dad-2618982_1280.jpg);
    background-attachment: fixed;
    background-size: cover;
}
section.Quotation{
    display: flex;
    background-color: var(--color-bg);
    border-radius: 30px;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    text-align: center;
    margin: 3rem 20%;
    padding: 0 2rem 2rem;
}
section.Quotation > h1 {
    font-size: 42px;
    color: var(--color-text-dark);
    margin: 2rem;
}
section.Quotation > p {
    font-size: larger;
    color: var(--color-text-dark);
    line-height: 2rem;
}
section.Quotation > span {
    font-size: 20px;
    margin-top: 1rem;
    padding: 1rem 2rem;
    border-top: 1px solid var(--color-text-dark);
}
@media (max-width: 690px) {
    section.Quotation{
        margin: 3rem 1rem;
    }
}